<template>
<div id='app'>
  <div v-if="isload==='A'"><ring-loader  class='v-spinner' :loading='loading' :color='color' :size='size'></ring-loader></div>
  <div v-else-if="isload==='B'" class='welcome'><p>ようこそ</p></div>
  <div v-else-if="isload==='C'" style='color: black;'>
    <div class='animation'>
      <header class='mainheader'>
        <router-link to='/'><img src='./assets/image/Vso59x2r_400x400.jpg' alt='MyBike:Ninja250' class='image'></router-link>
        <div class='siteinfo'>
          <router-link to='/'><h1>まるこめ日記</h1></router-link>
          <p>趣味やらなんやら書きます</p>
          <p class='UpdateDate'>最終更新：2018年6月26日(火)</p>
        </div>
      </header>
      <router-view/>
      <footer>
        <div class='footermain'>
        <a href='mailto:marukome.diary@gmail.com'><img src='./assets/image/freemailicon.png' alt='mail' class='image2'></a>
        <a href='https://twitter.com/Ka2Robo'><img src='./assets/image/Twitter_Social_Icon_Circle_Color.png' alt='Twitter' class='image2'></a>
        <a href='https://github.com/okomeee'><img src='./assets/image/Octocat.png' alt='Github' class='image2'></a>
      </div>
      </footer>
    </div>
  </div>
</div>
</template>

<script>
import RingLoader from 'vue-spinner/src/RingLoader.vue'
export default {
  components: {
    RingLoader
  },
  data: function () {
    return {
      color: '#fff',
      size: '50px',
      isload: 'A'
    }
  },
  created: function () {
    setTimeout(() => (this.isload = 'B'), 2000)
    setTimeout(() => (this.isload = 'C'), 4000)
  }
}
</script>
<style>
header {
  text-align: center;
}
body {
  margin: 0%;
  overflow-y: scroll;
  /* Copy from https://coliss.com/articles/build-websites/operation/css/css-responsive-full-background-image-by-sixrevisions.html*/
  background-image: url("./assets/image/blake-connally-373084-unsplash.jpg");
  background-position: top center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}
footer {
  margin-left: 10%;
  margin-right: 10%;
  text-align: right;
}
h1 {
  margin: 0;
  font-size: 40px;
}
h2 {
  margin: 0%;
}
p {
  margin: 1% 0%;
  font-size: 20px;
}
a {
  text-decoration: none;
  color: black;
}
article {
  display: inline-block;
  padding: 2%;
  margin-top: 0%;
  margin-bottom: 3%;
  margin-left: 3%;
  margin-right: 3%;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
  text-align: left;
}
#app {
  font-family: "MyFont";
  text-align: center;
}
.v-spinner {
  position: absolute;
  top: 20%;
  left: 45%;
}
.welcome {
  margin-top: 20%;
  text-align: center;
  animation: fadeInOut 2s ease-in-out forwards;
}
.welcome p {
  font-size: 36px;
  color: white;
}
.animation {
  width: 100%;
  animation: fadeIn 1s ease-in;
}
.mainheader {
  margin-top: 5%;
  margin-bottom: 10%;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
}
.siteinfo {
  margin: 1% 0;
  display: inline-block;
  vertical-align: middle;
}
.UpdateDate {
  font-size: 16px;
}
.image {
  width: 80px;
  height: 80px;
  border-radius: 10%;
  margin: 0.5%;
  vertical-align: middle;
}
.footermain {
  display: inline-block;
  margin-top: 3%;
  padding: 0.5% 1%;
  background-color: rgba(255, 255, 255, 0.5);
}
.image2 {
  width: 20px;
  height: 20px;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeInOut {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@font-face {
  font-family: "MyFont";
  src: url("./assets/fonts/春夏秋冬ⅡB.ttf");
}
</style>



