import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/pages/HomePage'
import AboutMe from '@/pages/AboutMe'
import Blog from '@/pages/Blog/Blog'
import RandomPair from '@/pages/product/RandomPair'
import AccelerationSensor from '@/pages/product/AccelerationSensor'
import puchi from '@/pages/product/puchi'
import sai from '@/pages/product/sai'
import RandomPrefecture from '@/pages/product/RandomPrefecture'
import B1 from '@/pages/Blog/B1'
import B2 from '@/pages/Blog/B2'
import B3 from '@/pages/Blog/B3'
import B4 from '@/pages/Blog/B4'
import B5 from '@/pages/Blog/B5'
import B6 from '@/pages/Blog/B6'
import B7 from '@/pages/Blog/B7'
import B8 from '@/pages/Blog/B8'
import B9 from '@/pages/Blog/B9'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [{
    path: '/',
    component: HomePage
  },
  {
    path: '/aboutme',
    component: AboutMe
  },
  {
    path: '/blog',
    component: Blog
  },
  {
    path: '/RandomPair',
    component: RandomPair
  },
  {
    path: '/AccelerationSensor',
    component: AccelerationSensor
  },
  {
    path: '/puchi',
    component: puchi
  },
  {
    path: '/sai',
    component: sai
  },
  {
    path: '/RandomPrefecture',
    component: RandomPrefecture
  },
  {
    path: '/B1',
    component: B1
  },
  {
    path: '/B2',
    component: B2
  },
  {
    path: '/B3',
    component: B3
  },
  {
    path: '/B4',
    component: B4
  },
  {
    path: '/B5',
    component: B5
  },
  {
    path: '/B6',
    component: B6
  },
  {
    path: '/B7',
    component: B7
  },
  {
    path: '/B8',
    component: B8
  },
  {
    path: '/B9',
    component: B9
  }]
})



